import React, { useState, useContext } from 'react';
import { Layout, Menu } from 'antd';
import {ContextType} from '../Config/context'
import {
  PieChartOutlined,
  FileOutlined,
  TeamOutlined,
  UserOutlined,
} from '@ant-design/icons';
const { SubMenu } = Menu;

let Sidebar = (props) => {
  let context = useContext(ContextType)
  const [collapsed, setCollapsed] = useState(true)

  const f_handleChange = function(e){
    context.f_setActivePage(e.key)
  }

  return (
    <Layout.Sider collapsed={collapsed}>
      <div className="logo">
      </div>
      <Menu theme="dark" onClick={f_handleChange} defaultSelectedKeys={[context.activePage]} mode="inline">
        <Menu.Item key="dashboard" icon={<FileOutlined />} />
        <Menu.Item key="map" icon={<PieChartOutlined />}>
          Maps
        </Menu.Item>
      </Menu>
    </ Layout.Sider>
  );
}

export default Sidebar;
