import React from 'react'

let ContextType = React.createContext()
const {Provider, Consumer} = ContextType

class ContextProvider extends React.Component{
  state = {
    activePage : 'map'
  }

  f_setActivePage = (value) => {
    this.setState({ activePage : value})
  }

  render(){
    return(
      <Provider value = {{
        ...this.state,
        f_setActivePage : this.f_setActivePage
      }}>
        {this.props.children}
      </Provider>
    )
  }
}

export{ ContextProvider, Consumer, ContextType}
