import React from 'react';
import ButtonNotif from './Components/ButtonNotif';
import Sidebar from './Containers/Sidebar'
import MapApp from './Containers/MapApp'
import Dashboard from './Containers/Dashboard'
import { ContextProvider, Consumer } from './Config/context'
import { Layout } from 'antd';
import './App.css';

const { Content } = Layout

function App() {
  return (
    <ContextProvider>
      <Layout style={{ minHeight: '100vh' }}>
        <Sidebar />
        <Layout className="site-layout">
          <Content style={{ margin: '0 0px' }}>
            <div className="site-layout-background" style={{ minHeight: '100vh' }}>
              <Consumer>
                {
                  context => {
                    if(context.activePage == 'dashboard') {
                      return <Dashboard />
                    } 
                    else if(context.activePage == 'map') {
                      return <MapApp />
                    }
                  }
                }
              </Consumer>
              {/* <MapApp /> */}
            </div>
          </Content>
        </Layout>
      </Layout>
    </ContextProvider>

  );
}

export default App;
