import React, { PureComponent } from 'react';
import {
  AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip,
} from 'recharts';

const data = [
  {
    name: 'Jan', area: 1.2,
  },
  {
    name: 'Feb', area: 2.2,
  },
  {
    name: 'Mar', area: 3.2,
  },
  {
    name: 'Apr', area: 2.9,
  },
  {
    name: 'May', area: 2.2,
  },
  {
    name: 'June', area: 1.9,
  },
  {
    name: 'Jul', area: 2.0,
  },
  {
    name: 'Aug', area: 0.5,
  },
  {
    name: 'Sep', area: 0.6,
  },
  {
    name: 'Oct', area: 0.2,
  },
  {
    name: 'Nov', area: 1.1,
  },
  {
    name: 'Dec', area: 1.3,
  },
];

export default class AreaCharts extends PureComponent {
  static jsfiddleUrl = 'https://jsfiddle.net/alidingling/Lrffmzfc/';

  render() {
    return (
      <>
      {/* <span className="title" >Deforestation by Month</span> */}
      <AreaChart
        width={900}
        height={100}
        data={data}
        margin={{
          top: 20, left: 20, bottom: 20, right : 20
        }}
      >
        <CartesianGrid strokeDasharray="3 3" />
        <XAxis dataKey="name" />
        <YAxis />
        <Tooltip />
        <Area type="monotone" dataKey="area" stroke="#8884d8" fill="#8884d8" />
      </AreaChart>
      </>
    );
  }
}

