import React, { useState } from 'react';
import ReactDOM from 'react-dom';
import {ContextType} from '../Config/context'
import { loadModules } from 'esri-loader';
import { Button, Descriptions, Divider, Drawer, Slider, Checkbox, Space, Result, Empty, Modal, InputNumber, Row, Col, Spin, Table } from 'antd'
import ButtonNotif from '../Components/ButtonNotif';
import ButtonClear from '../Components/ButtonClear';
import ButtonFire from '../Components/ButtonFire'
import DeforestationForm from '../Components/DeforestationForm';
import DfrsCard from '../Components/DfrsCard'
import ModalContent from '../Components/ModalContent'
import moment from 'moment'
import PickerButton from 'antd/lib/date-picker/PickerButton';
import jsPDF from 'jspdf'
import 'jspdf-autotable'
import MapView from './tes'
const _ = require('lodash');

const doc = new jsPDF({
  orientation: "landscape"
})

const WsEndPoint = "http://10.7.12.21:8000/service/WGService.asmx/"

class MapApp extends React.Component {
  static contextType = ContextType

  constructor(props, context) {
    super(props, context)
    this.mapRef = React.createRef()
    this.state = {
      isDrawerShow : false,
      dfrsResult : [],
      timedata : [],
      showModal : false,
      modalData : [],
      isLoading : false,
      dfrsPointGeom : [],
      dfrsAreaGeom : null,
      timeSelection : []
    }

    this.theme = {
      dark : "https://js.arcgis.com/4.16/esri/themes/dark/main.css",
      light : true,
    }

    this.f_fetchTimeDfrs()
  }

  //=============================================================
  // FUNCTION
  //=============================================================
  f_showDrawer = (showed) => {
    this.setState({
      isDrawerShow : showed
    })
  }

  f_dfrsFormOnFinish = (datamoment, type) => {
    let whereCond, timeSelection
    if(type == "range-picker"){
      let startDate = datamoment["range-picker"][0].format("YYYY-MM-DD")
      let endDate = datamoment["range-picker"][1].format("YYYY-MM-DD")
      // whereCond = `end_date >= '${startDate}' and end_date <= '${endDate}'`
      whereCond = `start_date >= date'${startDate}' and end_date <= date'${endDate}'`
      timeSelection = [startDate, endDate]
    }
    else if(type == "period"){
      let [start, end ] = datamoment["period-time"].split("|")
      whereCond = `end_date >= date'${start}' and end_date <= date'${end}'`
      // whereCond = `start_date >= '${start}' and end_date <= '${end}'`
      timeSelection = [start, end]
    }
    else if(type == "all"){
      whereCond = ``
      timeSelection = []
    }
    else if(type == "todate"){
      let [start, end] = this.f_getDatesRange(moment("20190215"), moment())[0]
      // console.log("TimeRange", todate )
      whereCond = `end_date >= date'${start}' and end_date <= date'${end}'`
      timeSelection = [start, end]
    }
    this.setState({ timeSelection : timeSelection })

    // FILTER UNTUK CARD SELECTION
    let query =  this.layerDeforestationPoint.createQuery()
    query.where = whereCond
    // this.layerDeforestationPoint.queryFeatures(query).then(response => this.setState({ dfrsResult : response.features }) )
    this.layerDeforestationArea.queryFeatures(query).then(response => this.setState({ dfrsResult : response.features }) )

    // FILTER DEFORESTASI DI LAYER
    this.layerDeforestationPoint.definitionExpression = whereCond
    this.layerDeforestationPoint.visible = true
  }

  f_zoomTo = (f) => {
    // console.log(lat, lon)
    // console.log("that features", f)
    this.view.goTo({ target : f.geometry, tilt : 70})

    // HIGHLIGHT
    this.view.whenLayerView(this.layerDeforestationArea).then(
      (layerview) => {
        let query = this.layerDeforestationArea.createQuery()
        query.where = `id = '${f.attributes.id}'`
        this.layerDeforestationArea.queryFeatures(query).then(
          result => {
            console.log("rf", result.features)
            if(this.highlightArea){
              this.highlightArea.remove()
            }

            this.highlightArea = layerview.highlight(result.features)

          } 
        )
      }
    )

    // this.view.center = [lon, lat]
    // this.view.zoom = 13
  }

  f_fetchTimeDfrs = () => {
    fetch(`https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Deforestation/MapServer/0/query?f=json&outFields=[start_date,end_date]&where=1=1&returnGeometry=false`)
      .then(response => response.json() )
      .then( data => this.setState({
        timedata : data.features
      }) )
  }

  f_clearBufferLayers = () => {
    this.bufferLayer.removeAll()
  }

  f_getDatesRange(startdate, enddate){
    let nextStartDate = startdate
    let arr = []
    while(nextStartDate <= enddate){
      let start = nextStartDate.format()
      let end = nextStartDate.add(13, 'days').format()
      arr.push([ start, end ])
      nextStartDate = nextStartDate.add(1, 'days')
    }
    return arr.reverse()
  }

  f_onClear = () => {
    this.setState({ dfrsResult : [] })
    this.layerDeforestationPoint.visible = false
  }

  f_hotspot = () => {
    let column = ['start date', 'end date', 'lat', 'lon', 'conssesion', 'mangrove', 'other', 'total']
    
  }

  f_downloadPdf = () => {
    console.log("data to pdf", this.state.dfrsResult)
  }
  //
  // LIFECYCLE
  //
  componentDidMount() {
    loadModules(['esri/Map', 'esri/views/MapView', 'esri/layers/VectorTileLayer', 'esri/widgets/BasemapGallery', 'esri/widgets/Expand', "esri/widgets/LayerList", "esri/layers/FeatureLayer", "esri/layers/GroupLayer", "esri/Graphic", "esri/layers/GraphicsLayer", "esri/widgets/Legend", "esri/geometry/geometryEngine", "esri/core/watchUtils", "esri/widgets/DistanceMeasurement2D", "esri/tasks/support/Query", "esri/widgets/TimeSlider", "esri/widgets/Zoom", "esri/geometry/projection", "esri/geometry/SpatialReference"], { css: this.theme.light }).then(
      ([ArcGISMap, MapView, VectorTileLayer, BasemapGallery, Expand, LayerList, FeatureLayer, GroupLayer, Graphic, GraphicsLayer, Legend, geometryEngine, watchUtils, DistanceMeasurement2D, Query, TimeSlider, Zoom, projection, SpatialReference]) => {
        //===============================================================
        // LOCAL COMPONENT
        //===============================================================
        // Table deskripsi pada popup information
        const DescriptionTable = (props) => {
          let [isTrace, setTrace] = useState(false)

          let data = props.data

          let detailDescription = []
          for (var key of Object.keys(data)) {
            detailDescription.push(<Descriptions.Item label={key} key={key} >{data[key]}</Descriptions.Item>)
          }

          // Function to draw spider map from POM to refinery
          let f_traceData = (id, layername, lon, lat) => {
            let url
            if (layername === "Refinery") {
              url = `${WsEndPoint}_GetPOMSupplier_?refinery=${id}`
              this.layerPom.visible = true
            } else if (layername === "POM") {
              url = `${WsEndPoint}_GetRefinery_?pom=${id}`
              this.layerRefinery.visible = true
            }

            fetch(url)
              .then(response => response.json())
              .then(resultData => {
                // DRAW LINE
                let lineSymbol = {
                  type: 'simple-line',
                  color: [226, 119, 40],
                  width: 2
                }

                resultData.map(el => {
                  let spiderLine = {
                    type: "polyline",
                    paths: [
                      // [props.position.longitude, props.position.latitude],
                      [lon, lat],
                      [parseFloat(el.X_Coor), parseFloat(el.Y_Coor)]
                    ]
                  }

                  let spiderLineGraphic = new Graphic({
                    geometry: spiderLine,
                    symbol: lineSymbol
                  })

                  this.view.graphics.add(spiderLineGraphic)
                })
              })
              .then(() => setTrace(true))
              .catch(error => console.log('error', error));

          }

          let f_clearGraphic = () => {
            this.view.graphics.removeAll()
            this.layerRefinery.visible = false
            setTrace(false)
          }

          let f_button = () => {
            switch (props.title) {
              case "POM":
                return isTrace ? <Button type="primary" onClick={f_clearGraphic} danger >Clear</Button> : <Button type="primary" onClick={() => f_traceData(data.pomid, props.title, props.position.longitude, props.position.latitude)} >Trace</Button>
                break
              case "Refinery":
                return isTrace == true ? <Button type="primary" onClick={f_clearGraphic} danger >Clear</Button> : <Button type="primary" onClick={() => f_traceData(data.rfid, props.title, props.position.longitude, props.position.latitude)} >Trace</Button>
                break
            }
          }

          return (
            <React.Fragment>
              <Descriptions title={props.title} style={{ fontSize: '12px' }} bordered column={1} size="small">
                {detailDescription}
              </Descriptions>
              <Divider />
              {f_button()}
            </React.Fragment>
          )
        }

        // Konten saat Deforestation point di klik
        const DfrsPopupContent = (props) => {
          console.log("props", props)
          this.layerPlantationArea.queryFeatures().then(r => console.log("conc area", r))
          const [inputValue, setInputValue] = useState(0)
          const [pomInside, setPomInside] = useState([])
          const [concInside, setConcInside] = useState([])
          const layerOptions = [
            { label : 'POM', value : this.layerPom, key:"1"  },
            { label : 'Reffinery', value : this.layerRefinery, key:"2" },
            { label : 'Concession Area', value : this.layerPlantationArea, key:"3" },
            { label : 'Peatland', value : this.layerPeat, key:"4" },
          ]

          // let geom = ( this.bufferLayer.graphics.getItemAt(0) !== undefined ) ? this.bufferLayer.graphics.getItemAt(0).geometry : false

          const updateLayerVisibility = e => {
            e.target.value.visible = e.target.checked
            checkFeatureInside()
          }

          const onChange = value => { 
            setInputValue(value)
            props.onSliderChange(value) 
            checkFeatureInside()
          }

          const checkFeatureInside = () => {
            layerOptions.forEach( (el, i) => {
              let layer = el.value
              //POM SUPPLIER
              if(layer.title == "POM Supplier"){
                if(layer.visible){
                  filterFeatureInside(layer).then(
                    result => setPomInside(result.features)
                  )
                }
                else{
                  setPomInside([])
                }
              }
              //KONSESI
              else if(layer.title == "Oil Palm Area"){
                if(layer.visible){
                  filterFeatureInside(layer).then(
                    async result => {
                      // CALCULATE AREA
                      let query = this.layerDeforestationArea.createQuery()
                      query.where = `id = '${props.id}'`
                      let dfFeatures = await this.layerDeforestationArea.queryFeatures(query)
                      var concessionResult = await result.features.map(
                        async feature => {
                          let f = feature
                          let area = await calculateArea(feature.geometry, dfFeatures.features[0].geometry)
                          f.area = area.area
                          f.intersectGeometry = area.geometry
                          console.log("f", f)
                          return f
                        }
                      )

                      Promise.all(concessionResult).then(
                        value => setConcInside(value)
                      )
                    }
                  )
                }
                else{
                  setConcInside([])
                }
              }
            })
          }

          const calculateArea = (geom1, geom2) => {
            // console.log("g1g2", geom1, geom2)
            return projection.load().then(
              () => {
                let outSpatialReference = new SpatialReference({
                  wkid: 3857 //Sphere_Sinusoidal projection
                });
                let g1 = projection.project(geom1, outSpatialReference)
                let g2 = projection.project(geom2, outSpatialReference)
                let isIntersect = geometryEngine.intersects(g1, g2)
                if(isIntersect){
                  // let intersectArea = geometryEngine.intersect(g1,g2)
                  let intersectArea = geometryEngine.intersect(g2,g1)
                  // let difference = geometryEngine.difference(g2, g1)
                  console.log("intersect area", intersectArea)
                  // var area = geometryEngine.geodesicArea(intersectArea, "hectares")
                  var area = { area : geometryEngine.geodesicArea(intersectArea, "hectares"), geometry : intersectArea }
                }
                else{
                  var area = ''
                }
                return area
              }
            )

          }

          const filterFeatureInside = layer => {
            let geom = ( this.bufferLayer.graphics.getItemAt(0) !== undefined ) ? this.bufferLayer.graphics.getItemAt(0).geometry : false
            // console.log('l', layer)
            let filter = new Query()
            filter.geometry = geom
            filter.spatialRelationship = "intersects"
            filter.returnGeometry = true
            // let result = await layer.queryFeatures(filter)
            // return await result
            return layer.queryFeatures(filter)
          }

          const buttonOnClick = () => {
            let geom = ( this.bufferLayer.graphics.getItemAt(0) !== undefined ) ? this.bufferLayer.graphics.getItemAt(0).geometry : false
            if(geom){

              // Query untuk mencari area yang berintersect dengan area buffer
              const query = new Query()
              query.geometry = geom
              query.spatialRelationship = "intersects"
              query.returnGeometry = true
              query.outFields = "*"
              query.outSpatialReference = { wkid: 4326 };

              // Query unntuk menyeleksi area deforestasi berdasarkan ID point
              const query2 = new Query
              query2.outFields = "*"
              query2.returnGeometry = true
              query2.where = `id = '${props.id}'`

              const queryHotspot = new Query
              queryHotspot.outFields = "*"
              queryHotspot.geometry = geom
              queryHotspot.returnGeometry = true
              queryHotspot.spatialRelationship = "intersects"
              queryHotspot.where = `Acq_Date between date'${moment( props.attributes.start_date ).format("YYYY-MM-DD")}' and '${ moment(props.attributes.end_date).format("YYYY-MM-DD")}'`

              // Setelah berhasil di query hasil akan dikirim ke modal, untuk dianalisis apakah di geometry tersebut terdapat POI yang terimpact
              this.layerDeforestationArea.queryFeatures(query2).then(
                result => { 
                  this.setState({
                    dfrsAreaGeom : result.features[0].geometry
                  })
                }
              )
              
              // Mengaktifkan loading spinner
              this.setState({ isLoading : true })

              // Mengeksekusi Query sebelum dikirim ke Modal data2 apa saja yang terindikasi bersinggungan dengan area buffer yaitu layer pom, area plantation dan peat
              Promise.all([this.layerPom.queryFeatures(query), this.layerPlantationArea.queryFeatures(query), this.layerPeat.queryFeatures(query), this.layerHotspot.queryFeatures(queryHotspot) ])
                .then(result => {
                  let featureSet = result.map( featureSet => featureSet.features )
                  console.log("fs", featureSet)
                  this.setState({ modalData : featureSet})
                })
                // Menonaktifkan spinner dan menampilkan modal
                .then( () => this.setState({showModal : true, isLoading : false}) )

            }
          }

          const zoomToFeature = (geom, record ) => {
            console.log("record", record)
            this.view.goTo({
              target : geom,
              tilt : 70
            })
          }

          const zoomToIntersect = (geom) => {
            this.layerIntersectPolygon.removeAll()
            var simpleFillSymbol = {
              type: "simple-fill",
              color: [227, 139, 79, 0.8],  // orange, opacity 80%
              outline: {
                color: [255, 255, 255],
                width: 1
              }
            };

            let polygonGraphic = new Graphic({
              geometry : geom,
              symbol : simpleFillSymbol
            })

            this.layerIntersectPolygon.add(polygonGraphic)

            this.view.goTo({
              target : geom,
              tilt : 70
            })

          }

          const createList = (data, title) => {
            console.log("data", data)

            // COLUMN FOR POM
            const columns = [
              {
                title: 'Name',
                dataIndex: 'name',
                key: 'name',
                render: text => <a>{text}</a>,
              },
              {
                title: 'Action',
                dataIndex: 'action',
                key: 'action',
                render : (geom, record) => {
                  console.log("re", record)
                  return <Button onClick={() => zoomToFeature(geom.geometry, record) } >go to</Button>
                }
              },
            ]

            // COLUMN FOR CONCESSION
            const concessionColumn =  [
              {
                title: 'Names',
                dataIndex: 'name',
                key: 'name',
                render: text => <a>{text}</a>,
              },
              {
                title: 'Impacted Area',
                dataIndex: 'area',
                key: 'area',
                render : area => isNaN(parseFloat(area).toFixed(3)) ? null : <span style={{color : 'red'}}>{parseFloat(area).toFixed(3)} Ha</span>
              },
              {
                title: 'Action',
                dataIndex: 'action',
                key: 'action',
                render : (geom, record) => {
                  // console.log("re", record)
                  // return <Button onClick={() => zoomToFeature(geom.geometry, record) } >go to</Button>
                  if( record.area === undefined ){
                    return <Button onClick={() => zoomToFeature(geom.geometry, record) } >go to</Button>
                  }
                  else{
                    return <Space>
                      <Button onClick={() => zoomToFeature(geom.geometry, record) } >go to</Button>
                      <Button onClick={() => zoomToIntersect (geom.intersectGeometry) } >impacted</Button>
                    </Space>
                  }
                }
              },
            ]

            const mapingField = {
              "POM" : "PomName",
              "Concession" : "Name",
            }

            function chooseColumn(){
              if(title=="POM"){ return columns}
              else if(title == "Concession"){ return concessionColumn}
            }

            if(data.length > 0){
              console.log(data)
              let tableData = data.map(el => { return { name : el.attributes[ mapingField[title] ], action : el, area : el.area, intersectGeometry : el.intersectGeometry } })
              return(
                <>
                  <h1>{title}</h1>
                  {
                    <div style={{padding : '10px'}}>
                      <Table columns={chooseColumn()} dataSource={tableData} size="small" />
                    </div>
                  }
                  <Divider />
                </>
              ) 
            }
            else{
              return null
            }
          }

          const findIntersection = (searchPolygon, graphics) => {
            return graphics.filter(graphic => geometryEngine.intersects(graphic.geometry, searchPolygon))
          }

          return(
            <>
            <Descriptions title="Detail" column={1} bordered>
              <Descriptions.Item label="Start">{moment(props.attributes.start_date).format("DD/MM/YYYY")}</Descriptions.Item>
              <Descriptions.Item label="End">{moment(props.attributes.end_date).format("DD/MM/YYYY")}</Descriptions.Item>
              <Descriptions.Item label="Total Area">{props.attributes.total} ha</Descriptions.Item>
            </Descriptions>
              <Divider />
              <h4>Buffer Radius in Kilometers</h4>
              <div style={{position : 'relative'}}>
                <Row>
                  <Col span={17} >
                    <Slider defaultValue={inputValue} max={50} value = { typeof inputValue ==='number'? inputValue : 0 }tooltipPlacement="bottom" onChange={ onChange } />
                  </Col>
                  <Col span={2}>
                    <InputNumber min={0} max={50} style={{ margin: '0 16px' }} value={inputValue} onChange={onChange} />
                  </Col>
                </Row>
              
              <Divider />
              <h4>Show or hide layer</h4>
              {
                layerOptions.map( opt => <Checkbox key={opt.key} value={opt.value} onChange={updateLayerVisibility} >{ opt.label }</Checkbox>)
              }
              <Divider />
              { createList(pomInside, "POM") }
              { createList(concInside, "Concession") }
              </div>
              <div style={{position : 'sticky', bottom : '0px'}}>
                <Space>
                  <Button type="primary" onClick={ buttonOnClick }>Show List</Button>
                  {/* <Button type="primary" onClick={()=>this.setState({showModal : true})} >Export to PDF</Button> */}
                </Space>
              </div>
            </>
          )
        }

        //===============================================================
        // BASE FUNCTION
        //===============================================================
        // Fungsi untuk menggambar buffer
        const updateBufferGraphic = ( geometry,size ) => {
          let polySym = {
            type: "simple-fill", // autocasts as new SimpleFillSymbol()
            color: [140, 140, 222, 0.5],
            outline: {
              color: [0, 0, 0, 0.5],
              width: 2
            }
          };
          if (size > 0){
            var bufferGeometry = geometryEngine.geodesicBuffer( geometry, size, "kilometers")
            if(this.bufferLayer.graphics.length === 0){
              this.bufferLayer.add(
                new Graphic({
                  geometry : bufferGeometry,
                  symbol : polySym
                })
              )
            } else {
              this.bufferLayer.graphics.getItemAt(0).geometry = bufferGeometry
            }
          } else{
            this.bufferLayer.removeAll()
          }
        }

        // Generate isi dari popup
        const generateContent = (target, title) => {
          console.log(target, title)
          
          let popupDiv = document.createElement('div')

          if(title === "POM"){
            pomPopupTemplate.content = popupDiv
            ReactDOM.render(<DescriptionTable data={target.graphic.attributes} title={title} position={{ latitude: target.graphic.geometry.latitude, longitude: target.graphic.geometry.longitude }} />, popupDiv)
          } 
          else if (title === "Deforestation Point" || title === "Deforestation Area"){
            dpPopupTemplate.content = popupDiv

            //// IF value on slider are changing, the buffer size will be automatic adjusting to that value
            const onSliderChange = (bufferSize) => {

              //// Buffer is calculated from centered point
              // let pointGeometry = target.graphic.geometry
              let pointGeometry = target.graphic.geometry
              this.setState({
                dfrsPointGeom : pointGeometry
              })
              updateBufferGraphic(pointGeometry, bufferSize)
            }

            const onCheckboxChange = layerList => {
              layerList.map( layer => {
                layer.visible = true
              })
            }

            ReactDOM.render(<DfrsPopupContent id={target.graphic.attributes.id} onSliderChange={onSliderChange} attributes={ target.graphic.attributes } />, popupDiv)
          }
          else{
            pomPopupTemplate.content = popupDiv
            ReactDOM.render(<DescriptionTable data={target.graphic.attributes} title={title} position={{ latitude: target.graphic.geometry.latitude, longitude: target.graphic.geometry.longitude }} />, popupDiv)
          }

          return popupDiv
        }

        //===============================================================
        // DOM
        //===============================================================
        // document.getElementById("button-form-deforestation").addEventListener('click', function(e){
        //   console.log(e)
        // })

        //===============================================================
        // MAP INIT
        //===============================================================
        this.map = new ArcGISMap({
          basemap: 'streets-night-vector'
        });

        //===============================================================
        // VIEW INIT
        //===============================================================
        this.view = new MapView({
          container: this.mapRef.current,
          map: this.map,
          center: [118, 0],
          zoom: 4,
          popup: {
            dockEnabled: true,
            dockOptions: {
              // Disables the dock button from the popup
              buttonEnabled: false,
              // Ignore the default sizes that trigger responsive docking
              breakpoint: false,
              position : 'top-left'
            }
          }
        });

        // this.view.when(
        //   () => {
        //     console.log("sukses", this.view) },
        //   function(err){console.log(err)}
        // )

        //==============================================================
        // LAYER
        //==============================================================
        
        // GROUP PLANTATION
        //
        this.layerPlantationPoint = new FeatureLayer({
          // url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Third_Party_Supplier/MapServer/0",
          url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/OilPalm_Plantation_TP_Supplier/MapServer/0",
          title: "Oil Palm Plantation",
          visible: false,
          outFields : ['*']
        });

        this.layerPlantationArea = new FeatureLayer({
          url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Third_Party_Supplier/MapServer/2",
          title: "Oil Palm Area",
          visible: false,
          outFields : ['*']
        });

        this.layerPeat = new FeatureLayer({
          url : "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Layer_Fungsi_Ekosistem_Gambut_201907_sde/MapServer/0",
          title : "Peat Ecosystem",
          visible : false,
          outFields : ['*']
        })
        
        // GROUP INDUSTRIES
        //
        this.layerPkCrushing = new FeatureLayer({
          url: "https://idjktsvr10.wil.local/arcgis/rest/services/industries_MIL1/MapServer/0",
          title: "3rd PK Crushing",
          visible: false
        });

        this.layerRefinery = new FeatureLayer({
          url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Layer_Refinery_201908_sde/MapServer/0",
          title: "Refinery",
          visible: false,
          popupEnabled: true,
          outFields: ['*']
        });

        this.layerPom = new FeatureLayer({
          // url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Third_Party_Supplier/MapServer/1",
          // url: "https://gisportal.wilmar.co.id/arcgisserver/rest/services/NewGISInteractiveMap_2/POM/MapServer/1",
          url: "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Third_Party_Supplier_2020/MapServer/1",
          title: "3rd Party Mill",
          visible: false,
          popupEnabled: true,
          outFields: ['pomid', 'PomName', 'CompanyNam', 'PlaceName', 'Tankcap', 'Silocap']
        });
        // this.layerPom = importedLayer.layerPom

        // GROUP DEFORESTATION
        //
        this.layerDeforestationPoint = new FeatureLayer({
          url : "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Deforestation/MapServer/0",
          title : "Deforestation Point",
          visible : false,
          popupEnabled: true,
          outFields : ['*']
        });
        this.map.add(this.layerDeforestationPoint)

        this.layerDeforestationArea = new FeatureLayer({
          // url : "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Deforestation/MapServer/1",
          url : "https://gisportal.wilmar.co.id/arcgisserver/rest/services/third_party/deforestation_builder/MapServer/0",
          title : "Deforestation Area",
          visible : false,
          outFields : ['*']
        });

        // Basemap WILMAR
        //
        var basemapWilmar = new VectorTileLayer({
          url: 'https://gisportal.wilmar.co.id/arcgisserver/rest/services/Hosted/Wilmar_Basemap2/VectorTileServer?f=pjson'
        });

        // HOTSPOT
        this.layerHotspot = new FeatureLayer({
          url : "https://idjktsvr10.wil.local/arcgis/rest/services/NewGISInteractiveMap/Hotspot_HSP1/MapServer/0",
          title : "Hotspot",
          visible : false,
          outFields : ['*']
        })
        this.map.add(this.layerHotspot)

        // SKETCH LAYER
        //
        this.sketchLayer = new GraphicsLayer({
          listMode : "hide"
        })
        this.bufferLayer = new GraphicsLayer({
          listMode : "hide"
        })
        this.map.addMany([this.sketchLayer, this.bufferLayer])

        // LAYER GROUPING
        //
        var glIndustries = new GroupLayer({
          title: "Wilmar Third Party"
        });

        var glPlantation = new GroupLayer({
          title : "Plantation"
        });

        var glDeforestation = new GroupLayer({
          title : "Deforestation"
        })

        var glLandIssue = new GroupLayer({
          title : "Land Issue"
        })

        glIndustries.addMany([ this.layerRefinery, this.layerPom, this.layerPkCrushing, this.layerPlantationArea, this.layerPlantationPoint, ]);
        // glPlantation.addMany([ this.layerPlantationArea, this.layerPlantationPoint, ])
        glDeforestation.addMany([ this.layerDeforestationArea, this.layerDeforestationPoint,  ])
        glLandIssue.addMany([this.layerPeat])
        this.map.addMany([ glIndustries, glDeforestation, glLandIssue]);

        //// LAYER INTERSECT POLYGON
        this.layerIntersectPolygon = new GraphicsLayer({
          title : "intersected"
        })
        this.map.add(this.layerIntersectPolygon)

        //=================================================================
        // WIDGET
        //=================================================================
        
        // ZOOM
        var zoomWidget = new Zoom({
          view : this.view
        })
        this.view.ui.components = []
        this.view.ui.add(zoomWidget, "top-right");

        // this.view.whenLayerView(this.layerPom).then((lv)=> console.log("lv",lv))
        // BASEMAP GALERY
        //
        var basemapGallery = new BasemapGallery({
          view: this.view
        });
        var expand = new Expand({
          view: this.view,
          content: basemapGallery,
          expandTooltip: 'Change Basemap'
        });
        this.view.ui.add(expand, "top-right");

        // LAYERLIST
        //
        var layerList = new LayerList({
          view: this.view,
        })
        var expandLayerList = new Expand({
          view: this.view,
          content: layerList,
          expandTooltip: 'Layer List'
        })
        this.view.ui.add(expandLayerList, "top-right")

        // LEGEND
        //
        var legend = new Legend({
          view : this.view,
        })
        this.view.ui.add(legend, "bottom-right")

        //
        //// TIMESLIDER
        //
        const timeSlider = new TimeSlider({
          container: "timeSlider",
          playRate: 50,
          stops: {
            interval: {
              value: 1,
              unit: "hours"
            }
          }
        });
        // this.view.ui.add(timeSlider, "manual");

        //=================================================================
        //POPUP SET
        //=================================================================
        let pomPopupTemplate = {
          title: 'POM Suplier',
          content: (target) => generateContent(target, "POM")
        }
        this.layerPom.popupTemplate = pomPopupTemplate

        let refineryPopupTemplate = {
          title: 'Refinery',
          content: (target) => generateContent(target, "Refinery")
        }
        this.layerRefinery.popupTemplate = refineryPopupTemplate

        let concAreaTemplate = {
          title : "Concession Area",
          content : target => generateContent(target, "Concession Area")
        }
        this.layerPlantationArea.popupTemplate = concAreaTemplate

        let hsTemplate = {
          title : "Hotspot",
          content : target => generateContent(target, "Hotspot")
        }
        this.layerHotspot.popupTemplate = hsTemplate

        let dpPopupTemplate = {
          title : 'Deforestation Point',
          content : target => generateContent(target, "Deforestation Point")
        }
        this.layerDeforestationPoint.popupTemplate = dpPopupTemplate 

        let daPopupTemplate = {
          title : 'Deforestation Area',
          content : target => generateContent(target, "Deforestation Area")
        }
        this.layerDeforestationArea.popupTemplate = daPopupTemplate

        //==================================================================
        // EVENT
        //==================================================================
        this.view.when(
          () => this.view.whenLayerView(this.layerIntersectPolygon)
            .then(
              layerview1 => {
                this.view.on("pointer-move", evt => {
                  if( this.layerIntersectPolygon.graphics.getItemAt(0) !== undefined ){
                    var screenPoint = { x : evt.x, y : evt.y }
                    this.view.hitTest(screenPoint).then( response => {
                      response.results.forEach( el => {
                        if (el.graphic.layer.title == 'intersected'){
                          console.log("elelee", el)
                        }
                      })
                    })
                  }
                })
              }
            )
        )

        watchUtils.whenTrue(this.view, "stationary", () => {
          if(this.view.zoom >= 10){
            console.log(this.view.zoom)
            this.layerDeforestationArea.visible = this.layerDeforestationPoint.visible ? true : false 
          }
          else{
            this.layerDeforestationArea.visible = false
          }
        })
      });


  }

  componentDidUpdate(){
    // console.log(this.context)
    // this.layerPlantationPoint.visible = true
  }

  componentWillMount() {
    if (this.view) {
      this.view.container = null
    }
  }

  render() {
    let dfrsContent
    if(this.state.dfrsResult == null){
      dfrsContent = <Result title="Please choose date range" />
    }
    else if(this.state.dfrsResult.length == 0){
      dfrsContent = <Empty />
    } 
    else {
      let content = []
      const impactedId = ['IDN_w0tv4g7_20200908', 'IDN_w0tv675_20200908', 'IDN_w0tv5sw_20200908', 'IDN_w0sxfr1_20200901', '	IDN_w0u8138_20200906']
      this.state.dfrsResult.map( (features, index) => {

        let obj = {
          key : index,
          lat : features.attributes.POINT_X,
          lon : features.attributes.POINT_Y,
          startdate : features.attributes.start_date,
          enddate : features.attributes.end_date,
          peatland : features.attributes.peatland,
          mangrove : features.attributes.mangrove,
          other : features.attributes.other,
          total : features.attributes.total,
          konsesi : parseFloat( features.attributes.SUM_konses / 1000 ).toFixed(2),
          peat : parseFloat( features.attributes.SUM_peat_ / 1000).toFixed(2),
          // zoomTo : ()=>this.f_zoomTo(features.attributes.POINT_Y, features.attributes.POINT_X, features),
          zoomTo : ()=>this.f_zoomTo(features),
          // impacted : impactedId.includes(features.attributes.id) ? true : false
          impacted : features.attributes.SUM_konses !== 0 ? true : false
        }

        // let sorter = impactedId.includes(features.attributes.id) ? 1 : 0
        let sorter =features.attributes.SUM_konses !== 0 ? 1 : 0
        content.push({sorter : sorter, data : obj})

      })

      let c = _.orderBy(content, ['sorter'], ['desc'])
      console.log(c)
      let d = _.map(c, function(el, i){
        let dat = {...el.data, index : i + 1}

        return <DfrsCard key={dat.key} lat={dat.lat} lon={dat.lon} startdate={dat.startdate} enddate={dat.enddate} peatland={dat.peatland} mangrove={dat.mangrove} konsesi={dat.konsesi} peat={dat.peat} other={dat.other} total={dat.total} index={dat.index} zoomTo={dat.zoomTo} impacted={dat.impacted} />
      })

        dfrsContent = <Space direction="vertical">{d}</Space>
    }

    return (
      <React.Fragment>
        <div className="webmap" ref={this.mapRef} />
        <div className="spinner">
          <Spin spinning={this.state.isLoading} size="large" tip="Loading..." />
        </div>
        {/* <div id="timeSlider"></div> */}
        <ButtonNotif onClick={ ()=>this.f_showDrawer(true) } count={4} />
        <ButtonClear onClick={ this.f_clearBufferLayers }  />
        <ButtonFire className="button-hotspot" onClick={ () => this.layerHotspot.visible = ! this.layerHotspot.visible } />
        <Drawer
          title="Deforestation"
          placement="right"
          closable={true}
          onClose={ () => this.f_showDrawer(false) }
          visible={this.state.isDrawerShow}
          width={500}
        >
          <DeforestationForm timesRange={this.f_getDatesRange(moment("20190215"), moment())} onFinish={this.f_dfrsFormOnFinish} onClear={this.f_onClear} />
          <Divider />
          {
            this.state.dfrsResult.length > 0 ? <Button onClick={this.f_downloadPdf} block>Download PDF</Button> : null
          }
          <Divider />
          <div style={{display : 'flex', flexDirection : 'column', justifyContent : 'space-between'}}>
            {dfrsContent}
          </div>
        </Drawer>
        <Modal
          title="Detail"
          visible={this.state.showModal}
          onOk={(e)=>this.setState({showModal : false})}
          onCancel={(e)=>this.setState({showModal : false})}
          width="80vw"
          destroyOnClose={true}
        >
          <ModalContent data={this.state.modalData} location={this.state.dfrsPointGeom} dfrsArea={this.state.dfrsAreaGeom} />
        </Modal>

      </React.Fragment>
    )
  }
}

export default MapApp